 body {
   height: 100vh;
   background: rgb(204, 146, 255);
   background: linear-gradient(0deg, rgba(204, 146, 255, 1) 0%, rgba(255, 51, 163, 0.4164040616246498) 100%);
   text-align: center;
 }

 body * {
   font-family: 'Permanent Marker', cursive;
 }

 h1 {
   display: flex;
   justify-content: center;
   color: #9CDB0B;
   transform: translate(-5%, -5%);
   font-size: 2rem;
   animation-duration: 8s;
   animation-name: move;
   animation-iteration-count: infinite;
   animation-timing-function: ease;
   -webkit-text-stroke-width: 0.1px;
   -webkit-text-stroke-color: black;
 }

 @keyframes move {
   from {
     color: black;
     -webkit-text-stroke-width: 0.1px;
   }

   to {
     color: #9CDB0B;
     -webkit-text-stroke-width: 1px;
   }
 }

 #play_new_game {
  position: relative;
  display: flex;
  margin: 0 auto;
  bottom: -2rem;
  height: 4rem;
  width: 8rem;
  font-weight: 800;
  text-align: center;
  border-radius: 2px;
  background: #9CDB0B;
  box-shadow: 20px 20px 60px #09AD09,
    -20px -20px 60px #ffffff;
 }


 /*
*
*	DRAW LIFE
*
*/
 #life {
   margin-top: 2rem;
   text-align: center;
   margin-bottom: 2rem;
 }

 #life .heart.empty {
   opacity: 0.1;
 }

 .heart {
   background-color: red;
   display: inline-block;
   height: 20px;
   margin: 0 10px;
   position: relative;
   top: 0;
   transform: rotate(-45deg);
   width: 20px;
 }

 .heart:before,
 .heart:after {
   content: "";
   background-color: red;
   border-radius: 50%;
   height: 20px;
   position: absolute;
   width: 20px;
 }

 .heart:before {
   top: -10px;
   left: 0;
 }

 .heart:after {
   left: 10px;
   top: 0;
 }

 /*
*
*	END GAME MESSAGE
*
*/
 #win_message {
   font-size: 40px;
   color: white;
   padding: 10px;
 }

 #lost_message {
   font-size: 40px;
   color: red;
   padding: 10px;
 }

 /*
*
*	WORD TO FIND
*
*/
 #wrapper_new_word {
   border: 1px solid transparent;
   width: auto;
   text-align: center;
   margin: 0 auto;
   margin-top: 2rem;
 }

 #wrapper_new_word span {
   display: inline-block;
   border-bottom: 3px solid white;
   font-size: 40px;
   padding: 5px 20px;
   color: black;
   margin: 0 5px;
 }

 #wrapper_new_word span.notfinded {
   color: rgba(255, 255, 255, 0.3);
 }

 #wrapper_new_word span.lost {
   color: black;
 }

 #wrapper_new_word::after {
   clear: both;
   content: " ";
   min-height: 1px;
   display: block;
 }

 /*
*
*	KEYBOARD PART
*
*/
 #keyboard {
   border: 1px solid transparent;
   width: 90%;
   max-width: 600px;
   margin: 0 auto;
   text-align: center;
   margin-top: 0rem;
 }

 #keyboard button {
  background: white;
  border: solid 1px rgb(80, 150, 80);
  margin: 0.1rem;
  padding: 5px 20px;
  color: black;
  border-radius: 10px;
  border-top: solid 2px rgba(0, 0, 0, 0.332);
  border-left: solid 2px rgba(0, 0, 0, 0.332);
  border-bottom: none;
  border-right: none;
  cursor: pointer;
 }

 #keyboard .used {
   opacity: 0.3;
 }